<template>
  <div class="stages">
    <el-tabs v-model="activeName" @tab-click="getStageCourse">
      <el-tab-pane label="试岗阶段" name="trial">
        <study />
      </el-tab-pane>
      <el-tab-pane
        v-for="(item, index) of stageContent"
        :key="index"
        :label="item.title"
        :name="item.id + ''"
        :disabled="index > $store.state.user.mts_user.stage"
      >
        <template v-if="item.title && item.title.indexOf('轮岗')">
          <stage-head
            :title="item.title"
            :task-info="taskInfo"
            :index="index"
          />
          <learning-list
            title="学习清单"
            type="study"
            :list="studyDetail.detail"
            :must-learn="mustLean[index]"
            :stage="item.title"
            @btnFinishStageTip="btnFinishStageTip"
          />
          <learning-list title="已学清单" type="finish" :list="learnedList" />
        </template>
        <rotation-stage v-else />
        <train :stage="activeName - 1" :count="count" />
        <simulation />
      </el-tab-pane>
    </el-tabs>
    <!--  除轮岗外首次提示  -->
    <tip
      v-if="phaseFirstTip"
      :tip-show.sync="phaseFirstTip"
      btn-text="开始学习"
      dia-style="dia-style"
      :close-hid="true"
      :model-close="false"
      @btnHandle="btnPhaseFirstTip"
    >
      <section v-if="!remindData.start_at" slot="headTip">
        <p class="sign">
          恭喜通过{{
            getStage($store.state.user.mts_user.stage) - 1 || '轮岗阶段'
          }}，进入{{
            getStage(
              $store.state.user.mts_user.stage,
              $store.state.user.mts_user.job_rotation_position
            )
          }}的培训学习，
        </p>
        <p class="sign">详细培训细则阅读培训阶段的学习计划</p>
      </section>
      <section v-if="remindData.start_at" slot="headTip">
        <p class="sign">
          {{
            lastLearnNum ===
            studyDetail.plan.stage_study_number[
              $store.state.user.mts_user.stage
            ]
              ? '恭喜上个工作日按时完成任务'
              : lastLearnNum >
                studyDetail.plan.stage_study_number[
                  $store.state.user.mts_user.stage
                ]
              ? '恭喜上个工作日超额完成任务'
              : '非常遗憾上个工作日没有完成任务'
          }}，
        </p>
        <p class="sign">再接再厉，争取早日完成内容学习</p>
      </section>
      <section slot="content">
        <div>今日任务清单：</div>
        <div class="list">
          <p v-for="(item, index) of taskList" :key="index" class="sign">
            {{ item.title }}
          </p>
        </div>
      </section>
    </tip>
    <tip
      v-if="finishStageTip"
      :tip-show.sync="finishStageTip"
      btn-text="请求阶段考试"
      dia-style="dia-style"
      class="center"
      @btnHandle="btnFinishStageTip"
    >
      <section slot="content">
        <p>{{ finishTip }}</p>
        <p>该阶段内容已完成，可以申请考试</p>
      </section>
    </tip>
    <tip
      v-if="workShift"
      :tip-show.sync="workShift"
      btn-text="去轮岗"
      class="center"
      :has-footer="true"
      :close-hid="true"
      @btnHandle="btnWorkShiftShow"
    >
      <p v-if="remindData.real_day - remindData.record_day >= 0" slot="content">
        {{
          remindData.record_day === 1 ? '恭喜进入' : '目前处于'
        }}轮岗阶段，点击开始学习进入轮岗{{
          remindData.record_day !== remindData.real_day
            ? `第${remindData.record_day + 1}`
            : `最后一`
        }}天
      </p>
      <p v-if="remindData.record_day - remindData.real_day > 0" slot="content">
        目前处于轮岗阶段，轮岗阶段已超时{{
          remindData.record_day - remindData.real_day
        }}天
      </p>
    </tip>
    <el-dialog
      title="提示"
      :visible.sync="individualTraining"
      width="30%"
      center
      top="30vh"
    >
      <p style="text-align: center">
        该阶段单项训练未完成，请先前往完成单项训练版块
      </p>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="individualTraining = false"
          >确 定</el-button
        >
      </span>
    </el-dialog>
    <tip
      v-if="eliminateShow"
      :tip-show.sync="eliminateShow"
      btn-text="确定"
      class="center"
      :has-footer="true"
      :model-close="false"
      :close-hid="true"
      @btnHandle="btnEliminateShow"
    >
      <p slot="content">
        很遗憾未通过{{
          getStage(
            $store.state.user.mts_user.stage,
            $store.state.user.mts_user.job_rotation_position
          )
        }}，无法再继续向下进行
      </p>
    </tip>
    <el-dialog title="提示" :visible.sync="finishAllShow" width="30%" center>
      <p style="text-align: center;">
        恭喜完成培训阶段考核，准备相关资料，准备上线
      </p>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="finishAllShowClick">确 定</el-button>
      </span>
    </el-dialog>
    <tip
      v-if="delayShow"
      :tip-show.sync="delayShow"
      btn-text="确定"
      class="center"
      :has-footer="true"
      @btnHandle="btnDelayShow"
    >
      <p slot="content">
        很遗憾未通过{{
          getStage(
            $store.state.user.mts_user.stage,
            $store.state.user.mts_user.job_rotation_position
          )
        }}学习， 需要延期学习，重新进行考试
      </p>
    </tip>
  </div>
</template>

<script>
import StageHead from './components/StageHead'
import LearningList from './components/LearningList'
import Train from './components/Train'
import Simulation from './components/Simulation'
import RotationStage from './components/RotationStage'
import Tip from '@/components/Dialog'
import Study from './components/Study'
import { studyContent, getShowrtAnswerCount } from '@/api/learningAdmin'
import { createRemind, trialRemind, setStage, setPass } from '@/api/study'
import { getTime } from '@/utils/learningMethod'
export default {
  components: {
    StageHead,
    LearningList,
    Train,
    Simulation,
    RotationStage,
    Tip,
    Study
  },
  data() {
    return {
      activeName: '1',
      phaseFirstTip: false,
      stageContent: [],
      studyDetail: {},
      finishStageTip: false,
      taskInfo: {},
      learnedList: [], // 已学清单
      taskList: [],
      mustLean: [],
      studyTime: '0天0小时0分钟',
      workShift: false,
      remindData: {},
      finishTip: '',
      count: {},
      individualTraining: false,
      eliminateShow: false,
      finishAllShow: false,
      delayShow: false,
      lastLearnNum: 0
    }
  },
  computed: {
    getStage() {
      return function(stage, job_rotation_position) {
        if (stage === job_rotation_position) return '轮岗阶段'
        else {
          if (stage === -1) return '试岗阶段'
          if (stage < job_rotation_position) {
            if (stage === 0) return '第一阶段'
            if (stage === 1) return '第二阶段'
            if (stage === 2) return '第三阶段'
            if (stage === 3) return '第四阶段'
            if (stage === 4) return '第五阶段'
            if (stage === 5) return '第六阶段'
          }
          if (stage > job_rotation_position) {
            stage = stage - job_rotation_position
            if (stage === 0) return '第二阶段'
            if (stage === 1) return '第三阶段'
            if (stage === 2) return '第四阶段'
            if (stage === 3) return '第五阶段'
            if (stage === 4) return '第六阶段'
            if (stage === 5) return '第七阶段'
          }
        }
      }
    },
    isFinish() {
      return function(detail, list, len) {
        const finish = list.filter(item => {
          return (
            new Date().getDate() - new Date(item.exam_end_at).getDate() === 1
          )
        })
        if (finish.length === len) return '恭喜上个工作日按时完成任务，'
        if (finish.length > len) return '恭喜上个工作日超额完成任务，'
      }
    }
  },
  async created() {
    // this.finishStageTip = true
    await this.studyContent(this.$route.query.stage)
    await this.trialRemind()
    await this.getCount()
    if (this.$store.state.user.mts_user.stage_status === 'eliminate')
      this.eliminateShow = true
    // this.createRemind()
  },
  methods: {
    btnPhaseFirstTip() {
      this.createRemind()
      this.phaseFirstTip = false
    },
    async studyContent(stage) {
      const workShift = this.$store.state.user.mts_user.job_rotation_position
      const userStage = this.$store.state.user.mts_user.stage
      if (workShift === userStage && !stage && stage !== 0) stage = -2
      const { data } = await studyContent({
        stage:
          stage || stage === 0 ? stage : this.$store.state.user.mts_user.stage
      })
      if (data && data.result) {
        if (!data.stage_study_content) {
          this.$message({
            message: '您还没有领取计划不能进行阶段学习',
            type: 'error'
          })
          this.$router.push('/study/index')
        }
        this.studyDetail = data
        this.stageContent = data.stage_study_content
        this.taskList =
          data.plan && data.plan.stage_study_number
            ? data.detail.slice(0, data.plan.stage_study_number[data.stage])
            : []
        const finishList = data.list.filter(item => {
          return new Date(item.exam_end_at).getDate() === new Date().getDate()
        })
        this.lastLearnNum = data.list.filter(item => {
          return (
            new Date(item.exam_end_at).getDate() === new Date().getDate() - 1
          )
        }).length
        let total = 0
        this.mustLean = data.plan
          ? data.plan.stage_study_number.map(item => {
              if (
                this.remindData.start_at &&
                new Date().getDate() !==
                  new Date(this.remindData.start_at).getDate()
              )
                total =
                  (this.remindData.record_day || 1) * item - data.list.length
              return item - finishList.length + total
            })
          : []
        const time = data.list
          ? data.list.reduce((prev, next) => {
              return (
                prev +
                (new Date(next.study_at).getTime() - new Date(next.started_at))
              )
            }, 0)
          : 0
        this.studyTime = getTime({ time })
        this.taskInfo = {
          total_course: data.detail.length + data.list.length,
          finish: data.list.length,
          task_num:
            this.mustLean[stage || this.$store.state.user.mts_user.stage] < 0
              ? 0
              : this.mustLean[stage || this.$store.state.user.mts_user.stage],
          time: this.studyTime
        }
        this.learnedList = data.list.map(item => {
          return {
            title: item.catalog.title,
            id: item.catalog._id
          }
        })
        if (stage === -1 || this.$store.state.user.mts_user.stage === -1) {
          this.activeName = 'trial'
        } else {
          if (stage === -2) this.activeName = userStage + 1 + ''
          else
            this.activeName =
              stage || stage === 0
                ? stage + 1 + ''
                : 1 + this.$store.state.user.mts_user.stage + ''
        }
        if (data.detail.length === 0 && data.list.length !== 0) {
          this.finishTip = this.isFinish(
            data.detail,
            data.list,
            data.plan.stage_study_number[this.$store.state.user.mts_user.stage]
          )
          if (this.finishTip) this.finishStageTip = true
        }
        if (data.internship === 'pass') this.finishAllShow = true
        if (data.internship === 'delay') this.delayShow = true
      }
    },
    btnFinishStageTip() {
      if (
        this.$store.state.user.stage_status === 'eliminate' ||
        parseInt(this.activeName) - 1 !== this.$store.state.user.mts_user.stage
      )
        return
      if (this.count.diagnosis !== 0 && this.count.process !== 0) {
        this.individualTraining = true
        return
      }
      this.requestExamination()
      this.finishStageTip = false
    },
    async getCount() {
      const { data } = await getShowrtAnswerCount()
      this.count = data.count
    },
    createRemind() {
      createRemind({
        type:
          this.$store.state.user.mts_user.stage ===
          this.$store.state.user.mts_user.job_rotation_position
            ? 'job_rotation'
            : this.$store.state.user.mts_user.stage + 10 + ''
      })
    },
    getStageCourse(item) {
      this.studyContent(item.index - 1)
    },
    btnWorkShiftShow() {
      this.createRemind()
      this.workShift = false
    },
    async trialRemind(stage) {
      // 轮岗提醒
      const { code, data } = await trialRemind({
        stage: this.$store.state.user.mts_user.stage + 10
      })
      if (code === 20000 && data.result) {
        this.remindData = data
        const year = new Date(data.start_at).getFullYear()
        const month = new Date(data.start_at).getMonth()
        const day = new Date(data.start_at).getDate()
        const rTime = `${year}-${month}-${day}`
        const nowYear = new Date().getFullYear()
        const nowMonth = new Date().getMonth()
        const nowDay = new Date().getDate()
        const nowTime = `${nowYear}-${nowMonth}-${nowDay}`
        if (
          data.record_day !== data.real_day &&
          !stage &&
          this.$store.state.user.mts_user.stage ===
            this.$store.state.user.mts_user.job_rotation_position
        ) {
          if (rTime !== nowTime) this.workShift = true
        } else {
          if (
            rTime !== nowTime &&
            this.studyDetail.detail &&
            this.studyDetail.detail.length !== 0
          )
            this.phaseFirstTip = true
        }
      }
    },
    async requestExamination() {
      const { data, code } = await setStage()
      if (code === 20000) {
        if (data.result) this.$message({ message: '请求成功', type: 'success' })
        else this.$message({ message: data.message, type: 'error' })
      }
    },
    btnEliminateShow() {
      this.eliminateShow = false
    },
    async finishAllShowClick() {
      const { data, code } = await setPass()
      if (code === 20000 && data.result) this.finishAllShow = false
    },
    async btnDelayShow() {
      const { data, code } = await setPass()
      if (code === 20000 && data.result) this.delayShow = false
    }
  }
}
</script>

<style lang="scss" scoped type="text/scss">
.stages {
  /deep/.center {
    text-align: center;
  }
  /deep/h4 {
    padding: 10px 0;
  }
  /deep/.el-tabs__nav-wrap::after {
    height: 1px;
  }
  /deep/.el-tabs__item {
    padding: 0 37px;
    height: 50px;
    line-height: 50px;
  }
  /deep/.el-tabs--top .el-tabs__item.is-top:nth-child(2) {
    padding-left: 37px;
  }
  .sign {
    margin: 5px 0;
    line-height: 20px;
    font-size: 14px;
    color: #606266;
  }
}
</style>
