<template>
  <div class="learning-list">
    <h3 class="title">{{ title }}</h3>
    <div v-if="list.length" class="list">
      <dl>
        <dd
          v-for="(item, index) of list"
          :key="index"
          :class="item.locked ? 'no-lock' : 'lock'"
          @click="goStudy(item.locked, item.id)"
        >
          <img
            v-if="!item.locked"
            src="https://pic1.baobaohehu.com/fd/admin_v3/5d7f5f5c1c03121b82179aee//20200217173838450/1581932318169.png"
            alt="解锁"
          />
          <img
            v-else
            src="https://pic1.baobaohehu.com/fd/admin_v3/5d7f5f5c1c03121b82179aee//20200217173904846/1581932344552.png"
            alt="锁定"
          />
          <span class="text">
            &nbsp;<el-badge
              :value="mustLearn > index && type === 'study' ? '今日必学' : ''"
              class="item"
              >{{ type === 'study' ? '' : index + 1 + '.'
              }}{{ item.title }}</el-badge
            >
          </span>
        </dd>
      </dl>
      <div v-if="type === 'study'" class="tip">
        温馨提示：要同步学习单项训练各部分内容哦~
      </div>
    </div>
    <div v-if="!list.length && type === 'study'" class="no-list center">
      <img
        src="https://pic1.baobaohehu.com/fd/admin_v3/5d7f5f5c1c03121b82179aee//20200217173612303/1581932172024.png"
      />
      <p class="text">恭喜你，完成该阶段的学习任务，快去进行阶段考试吧~</p>
      <p class="text">等待考试时，记得复习哦~</p>
      <el-button type="primary" @click="$emit('btnFinishStageTip')"
        >请求阶段考试</el-button
      >
    </div>
    <div v-if="!list.length && type === 'finish'" class="no-finish center">
      <img
        src="https://pic1.baobaohehu.com/fd/admin_v3/5d7f5f5c1c03121b82179aee//20200217173529845/1581932129558.png"
      />
      <span>已学清单为空，赶紧去学习吧</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: 'study'
    },
    list: {
      type: Array,
      default: () => {
        return []
      }
    },
    mustLearn: {
      type: Number,
      default: 0
    },
    stage: {
      type: String,
      default: ''
    }
  },
  methods: {
    goStudy(isLock, id) {
      if (isLock) return
      this.$router.push('/study/course-detail/' + id)
    }
  }
}
</script>

<style lang="scss" scoped>
.learning-list {
  overflow-y: auto;
  margin: 15px 0 20px;
  padding: 20px;
  max-height: 344px;
  border: 1px solid #ebeef5;
  border-radius: 4px;
  .title {
    font-size: 16px;
    color: #17233d;
    margin-bottom: 20px;
  }
  .list {
    dd {
      position: relative;
      height: 38px;
      &::after {
        position: absolute;
        top: 14px;
        left: 5px;
        display: block;
        height: 24px;
        content: '';
        border-right: 2px solid rgb(235, 235, 237);
      }
      &.lock {
        cursor: pointer;
      }
      &.no-lock {
        cursor: not-allowed;
      }
      &:last-child {
        &::after {
          display: none;
        }
      }
      img {
        width: 12px;
        height: 14px;
      }
      .text {
        font-size: 14px;
        color: #576277;
      }
    }
    .tip {
      padding-left: 20px;
      font-size: 14px;
      color: #f8747f;
    }
  }
  .no-list {
    img {
      width: 168px;
      height: 100px;
      margin-bottom: 20px;
    }
    .text {
      font-size: 16px;
      color: #576277;
      margin-bottom: 8px;
    }
    /deep/button {
      margin: 10px;
      padding: 6px 20px;
      height: 32px;
    }
  }
  .no-finish {
    img {
      margin: 20px;
      width: 85px;
      height: 100px;
      vertical-align: middle;
    }
  }
}
</style>
