<template>
  <div class="train">
    <h3 class="title">单项训练</h3>
    <div class="card-wrap">
      <el-badge :value="diagnosis" class="item badge">
        <el-card
          class="box-card"
          @click.native="
            $router.push(
              '/study/user-problem-diagnosis/用户问题确诊?stage=' +
                stage +
                '&type=diagnosis'
            )
          "
        >
          <img
            src="https://pic1.baobaohehu.com/fd/admin_v3/5d7f5f5c1c03121b82179aee//20200217180057884/1581933657559.png"
            alt="用户问题确诊"
          />
          <span class="text">用户问题确诊</span>
        </el-card>
      </el-badge>
      <!--<el-card class="box-card">
        <img src="@/assets/images/shendufenxi@2x.png" alt="分析技巧">
        <span class="text">分析技巧</span>
      </el-card>-->
      <el-badge :value="process" class="item badge">
        <el-card
          class="box-card"
          @click.native="
            $router.push(
              '/study/user-problem-diagnosis/问诊训练?stage=' +
                stage +
                '&type=process'
            )
          "
        >
          <img
            src="https://pic1.baobaohehu.com/fd/admin_v3/5d7f5f5c1c03121b82179aee//20200217180155026/1581933714685.png"
            alt="问诊技巧"
          />
          <span class="text">问诊技巧</span>
        </el-card>
      </el-badge>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    stage: {
      type: [String, Number],
      default: 0
    },
    count: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  computed: {
    diagnosis() {
      if (
        !this.count.diagnosis ||
        parseInt(this.stage) !== this.$store.state.user.mts_user.stage
      )
        return ''
      return this.count.diagnosis + '题待解答'
    },
    process() {
      if (
        !this.count.process ||
        parseInt(this.stage) !== this.$store.state.user.mts_user.stage
      )
        return ''
      return this.count.process + '题待解答'
    }
  }
}
</script>

<style lang="scss" scoped type="text/scss">
.train {
  padding: 20px;
  border: 1px solid #ebeef5;
  border-radius: 4px;
  .title {
    font-size: 16px;
    color: #17233d;
    margin-bottom: 20px;
  }
  .card-wrap {
    display: flex;
  }
  .badge {
    /deep/.el-badge__content.is-fixed {
      right: 75px;
    }
  }
  .box-card {
    margin-right: 18px;
    width: 289px;
    height: 125px;
    cursor: pointer;
    /deep/.el-card__body {
      display: flex;
      align-items: center;
      justify-content: space-around;
      height: 100%;
      img {
        width: 75px;
        height: 75px;
      }
      .text {
        font-size: 16px;
        color: #17233d;
      }
    }
  }
}
</style>
